/*
* module dependencies
*/

import Reflux from 'reflux';
import $ from 'jquery';
import ImageActions from '../actions/ImageActions';

let ImageStore = Reflux.createStore({
    url: 'https://api.flickr.com/services/feeds/phptos_public.gne?format=json',
    listenables: [ImageActions],
    imageList: [],
    // Funcin para que se ejecute al inicio
    init: function() {
        this.fetchList();
    },
    fetchList: function() {
        let tags = ['animals', 'nature', 'travel', 'food', 'sport', 'cars'];
        let randomTag = tags[Math.floor(Math.random()*tags.length)];
        // request AJAX a la api
        $.ajax({
            url: this.url + `&tag=${randomTag}`,
            dataType: 'jsonp',
            jsonpCallback: 'jsonFlickrFeed',
            cache: false,
            context: this,
            success: function(data) {
                console.log('fetch ok');
                // segn lo que decuelve la api:
                this.imageList = data.items;
                // trigger del ImageStore que hace que se dispare la actualizacin de las vistas
                this.trigger(this.imageList);
            }
        })
    }
});

export default ImageStore;


