/*
* Module dependencies
*/

import React from 'react';
import Reflux from 'reflux';
import ImageStore from '../stores/ImageStore';

/*
Mixin necesario para que el componente reaccione a los cambios del store Reflux.
*/

// creando component segn ES5, no ES6-ES2015
let ImageGrid = React.createClass({
    mixins: [Reflux.connect(ImageStore, 'imagestore')],
    render: function() {
        if (this.state.imagestore) {
            return <div>
                {
                    this.state.imagestore.map( (image) => {
                        return <div className="image">
                            <a href="{image.link}"><img src={image.media.m} /></a>
                        </div>
                    })
                }
                </div>
        } else {
            return <p>Sin imgenes</p>
        }
    }
});

export default ImageGrid;


